// Style imports
import "normalize.css";
/*
normalize.css: a CSS reset, meaning that it normalizes initial style conditions on a page across browsers. It takes care of ensuring every browser has the same padding around a given HTML element, for example, or that the margins on the document itself are consistent across browsers. All of that stuff can be different from browser to browser, so using a reset helps ensure that the styles our app uses, which are in the main.css file, are applied on a stable, consistent foundation. You don’t use any styles in normalize.css yourself directly; they’re strictly something that gets applied automatically before your app’s styles do (which is why normalize.css must be imported first, by the way).
 */
import "../css/main.css";

// React imports
import React from "react";
import ReactDOM from "react-dom";

// App imports
import BaseLayout from "./components/BaseLayout";
import * as IMAP from "./IMAP";
import * as Contacts from "./Contacts";

// render the UI
const baseComponent = ReactDOM.render(<BaseLayout />, document.body);

// Now go fetch the user's mailboxes, and then their contacts.
baseComponent.state.showHidePleaseWait(true); // when true, display a “please wait” popup,
// so the user knows something is happening. This will also serve the purpose of blocking the UI for a moment
// so that the user can’t go and do something that causes problems while the server works
async function getMailboxes() {
  /*
  we get back an array of mailboxes, and we then iterate them and call the addMailboxToList() method on the state object (which we can do because we have a reference to the BaseLayout component via the baseComponent variable). That will update the mailboxes array in state, causing React to render the screen to show the mailboxes on the left.
   */
  const imapWorker: IMAP.Worker = new IMAP.Worker();
  const mailboxes: IMAP.IMailbox[] = await imapWorker.listMailboxes();
  mailboxes.forEach((inMailbox) => {
    baseComponent.state.addMailboxToList(inMailbox);
  });
}
getMailboxes().then(function () {
  // Now go fetch the user's contacts.
  async function getContacts() {
    const contactsWorker: Contacts.Worker = new Contacts.Worker();
    const contacts: Contacts.IContact[] = await contactsWorker.listContacts();
    contacts.forEach((inContact) => {
      baseComponent.state.addContactToList(inContact);
    });
  }
  getContacts().then(() => baseComponent.state.showHidePleaseWait(false));
});
/*
We don’t want to get the list of contacts until the list of mailboxes is done so that we know that all server calls are done before the please wait popup is hidden, so we use the then() syntax to chain them. Inside the then() callback, another function
is defined, getContacts() this time, for the same reason: async/await usage. Once defined, we can call getContacts() and again use the then() syntax so that we can call showHidePleaseWait(), passing false this time, to cause React to hide the please wait popup.
 */

