// App imports.
import * as Contacts from "./Contacts";
import { config } from "./config";
import * as IMAP from "./IMAP";
import * as SMTP from "./SMTP";

// defines an object that BaseLayout will include , and it's where all state
// for the application will live.

// this function will be called inside BaseLayout, and the state object will be returned
// that object will then become a member of BaseLayout
/**
 * This function must be called once and only once from BaseLayout.
 */
export function createState(inParentComponent) {
  // This function takes in a reference to the component that contains it, BaseLayout, in our case,
  // for reasons that will become apparent shortly. Then, this function simply returns an object, our state object:
  return {
    pleaseWaitVisible: false, // Flag: Is the please wait dialog visible?
    // This is a flag that will tell our code whether a “please wait” popup,
    // which we’ll show every time we call the server, is visible or not.
    contacts: [], // List of contacts.
    // We also need to maintain a list of contacts that the user has created, and that’s where the contacts array property comes in
    mailboxes: [], // List of mailboxes.
    // Similarly, a list of mailboxes is needed
    messages: [], // List of messages in the current mailbox.
    // And, assuming a mailbox has been selected, we need the list of messages within it
    currentView: "welcome", // The view that is currently showing ("welcome", "message", "compose", "contact" or "contactAdd").
    // When the user clicks a mailbox or clicks the NEW MESSAGE or NEW CONTACT button or clicks a contact in the list, what they see in the middle of the screen changes. This we refer to as the “view,” and what view is current must be known for React to render the correct content
    // It starts out with the "welcome" view and then changes to one of "welcome", "message" (when viewing a message), "compose" (when creating a message), "contact" (when viewing a contact), and "contactAdd" (when adding a contact).
    currentMailbox: null, // The currently selected mailbox, if any.
    // how we know what the currently selected mailbox is

    // The details of the message currently being viewed or composed, if any:

    // what state is necessary when either viewing or creating a message:
    messageID: null, // would only ever be populated when viewing an existing message
    messageDate: null,
    messageFrom: null,
    messageTo: null,
    messageSubject: null,
    messageBody: null,

    // The details of the contact currently being viewed or added, if any:

    // when viewing or creating a contact, we’ll need some state too:
    contactID: null,
    contactName: null,
    contactEmail: null,

    // ------------------------------------------------------------------------------------------------
    // ------------------------------------ View Switch functions -------------------------------------
    // ------------------------------------------------------------------------------------------------

    /**
     * Shows or hides the please wait dialog during server calls.
     *
     * @param inVisible True to show the dialog, false to hide it.
     */
    // state mutator method
    /*
          with React, you don’t directly tell components to do things. Instead, you mutate state in some way, using the setState() method on the component that holds the state, which causes React to repaint the pertinent parts of the UI as needed.
           */
    showHidePleaseWait: function (this: any,inVisible: boolean): void {
      this.setState({ pleaseWaitVisible: inVisible });

      /*
                   In this case, to show the please wait popup, all we need to do is update the pleaseWaitVisible state attribute, setting it to true. React will then redraw the UI, and what will happen, something you’ll see later, is that the popup element will now
           be set to visible.
                    */
    }.bind(inParentComponent) /* End showHidePleaseWait(). */,

    /*
        The little problem I alluded to earlier is that since we are limited to calling the setState() method on the component that contains the state object, any code that tries to call it must execute within the context of that component. When we define a separate object for state as I did in order to break it out into its own source file, the this reference in the methods inside state won’t be what we need. In other words, any method inside the state object won’t have access to setState() because its execution context, at least in some instances, won’t be the BaseLayout component.
That’s where the bind() statements come in, and you’ll see these on every function in the state object. When the state object is constructed via the call to createState(), a reference to the BaseLayout instance was passed in. That’s what we bind all the mutator functions to. That way, they will always have access to setState() as we need. Note that if any of them needs to touch the state object itself, which they obviously would need to in at least some cases, they can do so by accessing this.state, since components always expose their state via that property.
         */

    /**
     * Show ContactView in view mode.
     *
     * @param inID    The ID of the contact to show.
     * @param inName  The name of the contact to show.
     * @oaram inEmail The Email address of the contact to show.
     */
    showContact: function (this: any,
      inID: string,
      inName: string,
      inEmail: string
    ): void {
      console.log("state.showContact()", inID, inName, inEmail);

      this.setState({
        currentView: "contact",
        contactID: inID,
        contactName: inName,
        contactEmail: inEmail,
      });
    }.bind(inParentComponent) /* End showContact(). */,

    /**
     * Show ContactView in add mode.
     */
    showAddContact: function (this: any,): void {
      console.log("state.showAddContact()");

      this.setState({
        currentView: "contactAdd",
        contactID: null,
        contactName: "",
        contactEmail: "",
      });
    }.bind(inParentComponent) /* End showAddContact(). */,

    /**
     * Show MessageView in view mode.
     *
     * @param inMessage The message object that was clicked.
     */
    showMessage: async function (this: any, inMessage: IMAP.IMessage): Promise<void> {
      console.log("state.showMessage()", inMessage);

      // Get the message's body.
      this.state.showHidePleaseWait(true);
      const imapWorker: IMAP.Worker = new IMAP.Worker();
      const mb: String = await imapWorker.getMessageBody(
        inMessage.id,
        this.state.currentMailbox
      );
      this.state.showHidePleaseWait(false);

      // Update state.
      this.setState({
        currentView: "message",
        messageID: inMessage.id,
        messageDate: inMessage.date,
        messageFrom: inMessage.from,
        messageTo: "",
        messageSubject: inMessage.subject,
        messageBody: mb,
      });
    }.bind(inParentComponent) /* End showMessage(). */,

    /**
     * Show MessageView in compose mode.
     *
     * @param inType Pass "new" if this is a new message, "reply" if it's a reply to the message currently being
     *                    viewed, and "contact" if it's a message to the contact currently being viewed.
     */
    showComposeMessage: function (this: any, inType: string): void {
      console.log("state.showComposeMessage()");

      switch (inType) {
        case "new":
          this.setState({
            currentView: "compose",
            messageTo: "",
            messageSubject: "",
            messageBody: "",
            messageFrom: config.userEmail,
          });
          break;

        case "reply":
          this.setState({
            currentView: "compose",
            messageTo: this.state.messageFrom,
            messageSubject: `Re: ${this.state.messageSubject}`,
            messageBody: `\n\n---- Original Message ----\n\n${this.state.messageBody}`,
            messageFrom: config.userEmail,
          });
          break;

        case "contact":
          this.setState({
            currentView: "compose",
            messageTo: this.state.contactEmail,
            messageSubject: "",
            messageBody: "",
            messageFrom: config.userEmail,
          });
          break;
      }
    }.bind(inParentComponent) /* End showComposeMessage(). */,
    // ------------------------------------------------------------------------------------------------
    // ---------------------------------------- List functions ----------------------------------------
    // ------------------------------------------------------------------------------------------------

    /**
     * Add a mailbox to the list of mailboxes.
     *
     * @param inMailbox A mailbox descriptor object.
     */
    addMailboxToList: function (this: any, inMailbox: IMAP.IMailbox): void {
      console.log("state.addMailboxToList()", inMailbox);
      /*
First, you have always to remember that when you call setState(), you should never pass references to objects in state. That may sound weird, but it’s easy to understand when dealing with arrays, as we are here. Your first inclination would be to directly push
inMailbox into state.mailboxes and then try to call this.setState({this.state. mailboxes}). Everyone tries that at first because it seems reasonable! However, it won’t work because what you pass into setState() replaces what’s in state at the time, and trying to do that with what’s already there... well, let’s just say React won’t like you very much!
Instead, we make a copy of the array via slice(0), then push the new mailbox into that copy, and finally pass that copy to setState(). Now, everything works as expected. Note that you only have to do this sort of copying/updating/setting when dealing with objects and collections.
 */
      // Copy list.
      const cl: IMAP.IMailbox[] = this.state.mailboxes.slice(0);

      // Add new element.
      cl.push(inMailbox);

      // Update list in state.
      this.setState({ mailboxes: cl });
    }.bind(inParentComponent) /* End addMailboxToList(). */,

    /**
     * Add a contact to the list of contacts.
     *
     * @param inContact A contact descriptor object.
     */
    addContactToList: function (this: any, inContact: Contacts.IContact): void {
      console.log("state.addContactToList()", inContact);

      // Copy list.
      const cl = this.state.contacts.slice(0);

      // Add new element.
      cl.push({
        _id: inContact._id,
        name: inContact.name,
        email: inContact.email,
      });

      // Update list in state.
      this.setState({ contacts: cl });
    }.bind(inParentComponent) /* End addContactToList(). */,

    /**
     * Add a message to the list of messages in the current mailbox.
     *
     * @param inMessage A message descriptor object.
     */
    addMessageToList: function (this: any,inMessage: IMAP.IMessage): void {
      console.log("state.addMessageToList()", inMessage);

      // Copy list.
      const cl = this.state.messages.slice(0);

      // Add new element.
      cl.push({
        id: inMessage.id,
        date: inMessage.date,
        from: inMessage.from,
        subject: inMessage.subject,
      });

      // Update list in state.
      this.setState({ messages: cl });
    }.bind(inParentComponent) /* End addMessageToList(). */,

    /**
     * Clear the list of messages currently displayed.
     */
    clearMessages: function (this: any,): void {
      console.log("state.clearMessages()");

      this.setState({ messages: [] });
    }.bind(inParentComponent) /* End clearMessages(). */,

    // ------------------------------------------------------------------------------------------------
    // ------------------------------------ Event Handler functions -----------------------------------
    // ------------------------------------------------------------------------------------------------

    /**
     * Set the current mailbox.
     *
     * @param inPath The path of the current mailbox.
     */
    setCurrentMailbox: function (this: any,inPath: String): void {
      console.log("state.setCurrentMailbox()", inPath);

      // Update state.
      this.setState({ currentView: "welcome", currentMailbox: inPath });

      // Now go get the list of messages for the mailbox.
      this.state.getMessages(inPath);
    }.bind(inParentComponent) /* End setCurrentMailbox(). */,

    /**
     * Get a list of messages in the currently selected mailbox, if any.
     *
     * @param inPath The path to the mailbox to get messages for.  Note that because this method is called when the
     *               current mailbox is set, we can't count on state having been updated by the time this is called,
     *               hence why the mailbox is passed in.  This avoids the problem with setState() being asynchronous.
     */
    getMessages: async function (this: any, inPath: string): Promise<void> {
      console.log("state.getMessages()");

      this.state.showHidePleaseWait(true);
      const imapWorker: IMAP.Worker = new IMAP.Worker();
      const messages: IMAP.IMessage[] = await imapWorker.listMessages(inPath);
      this.state.showHidePleaseWait(false);

      this.state.clearMessages();
      messages.forEach((inMessage: IMAP.IMessage) => {
        this.state.addMessageToList(inMessage);
      });
    }.bind(inParentComponent) /* End getMessages(). */,

    /**
     * Fires any time the user types in an editable field.
     *
     * @param inEvent The event object generated by the keypress.
     */
    fieldChangeHandler: function (this: any, inEvent: any): void {
      console.log(
        "state.fieldChangeHandler()",
        inEvent.target.id,
        inEvent.target.value
      );

      // Enforce max length for contact name.
      if (
        inEvent.target.id === "contactName" &&
        inEvent.target.value.length > 16
      ) {
        return;
      }

      this.setState({ [inEvent.target.id]: inEvent.target.value });
    }.bind(inParentComponent) /* End fieldChangeHandler(). */,

    /**
     * Save contact.
     */
    saveContact: async function (this: any,): Promise<void> {
      console.log(
        "state.saveContact()",
        this.state.contactID,
        this.state.contactName,
        this.state.contactEmail
      );

      // Copy list.
      const cl = this.state.contacts.slice(0);

      // Save to server.
      this.state.showHidePleaseWait(true);
      const contactsWorker: Contacts.Worker = new Contacts.Worker();
      const contact: Contacts.IContact = await contactsWorker.addContact({
        name: this.state.contactName,
        email: this.state.contactEmail,
      });
      this.state.showHidePleaseWait(false);

      // Add to list.
      cl.push(contact);

      // Update state.
      this.setState({
        contacts: cl,
        contactID: null,
        contactName: "",
        contactEmail: "",
      });
    }.bind(inParentComponent) /* End saveContact(). */,

    /**
     * Delete the currently viewed contact.
     */
    deleteContact: async function (this: any,): Promise<void> {
      console.log("state.deleteContact()", this.state.contactID);

      // Delete from server.
      this.state.showHidePleaseWait(true);
      const contactsWorker: Contacts.Worker = new Contacts.Worker();
      await contactsWorker.deleteContact(this.state.contactID);
      this.state.showHidePleaseWait(false);

      // Remove from list.
      const cl = this.state.contacts.filter(
        (inElement) => inElement._id != this.state.contactID
      );

      // Update state.
      this.setState({
        contacts: cl,
        contactID: null,
        contactName: "",
        contactEmail: "",
      });
    }.bind(inParentComponent) /* End deleteContact(). */,

    /**
     * Delete the currently viewed message.
     */
    deleteMessage: async function (this: any,): Promise<void> {
      console.log("state.deleteMessage()", this.state.messageID);

      // Delete from server.
      this.state.showHidePleaseWait(true);
      const imapWorker: IMAP.Worker = new IMAP.Worker();
      await imapWorker.deleteMessage(
        this.state.messageID,
        this.state.currentMailbox
      );
      this.state.showHidePleaseWait(false);

      // Remove from list.
      const cl = this.state.messages.filter(
        (inElement) => inElement.id != this.state.messageID
      );

      // Update state.
      this.setState({ messages: cl, currentView: "welcome" });
    }.bind(inParentComponent) /* End deleteMessage(). */,

    /**
     * Delete a message (from the server and the contact list).
     */
    sendMessage: async function (this: any,): Promise<void> {
      console.log(
        "state.sendMessage()",
        this.state.messageTo,
        this.state.messageFrom,
        this.state.messageSubject,
        this.state.messageBody
      );

      // Send the message.
      this.state.showHidePleaseWait(true);
      const smtpWorker: SMTP.Worker = new SMTP.Worker();
      await smtpWorker.sendMessage(
        this.state.messageTo,
        this.state.messageFrom,
        this.state.messageSubject,
        this.state.messageBody
      );
      this.state.showHidePleaseWait(false);

      // Update state.
      this.setState({ currentView: "welcome" });
    }.bind(inParentComponent) /* End sendMessage(). */,
  };
}
